<template>
  <div class="photos" style="width:100%;">
    <ul class="photos_ul" style="width:100%;">
      <a v-for="(link,index) in links" :href="link" :key="link" target="_blank">
        <img v-if="!dark_mode && photo_see[index]" :class="class_name" :src="images_light[index]"/>
        <img v-if="dark_mode && photo_see[index]" :class="class_name" :src="images_dark[index]"/>
      </a>
    </ul>
    <iframe allowfullscreen="true" scrolling="no" border="0" frameborder="no" framespacing="0" v-if="kind=='Video'"
            src="./video/video.html"></iframe>
    <img class="qr" v-if="!dark_mode" src="/photos/share_2.png"/>
    <img class="qr" v-if="dark_mode" src="/photos_dark/share_2.png"/>
  </div>
</template>

<script>
export default {
  name: "Photos",
  props: {
    kind: String
  },
  data() {
    return {
      class_name: "imgphoto_pc",
      photo_see: [],
      photokind: {
        Experience_sharing: [20],
        Department_culture: [1, 2, 6, 12, 14, 15, 18, 19, 21, 23, 27, 29, 30, 33],
        Document_sharing: [5, 10, 24, 28, 34],
        Medical_science_popularization: [3, 4, 13, 25, 31],
        Psychological_words: [7, 11, 16],
        Intensive_care: [8, 32],
        Medical_English: [9, 17, 22, 26],
      },
      mediaQuery: window.matchMedia('(prefers-color-scheme: dark)'),
      dark_mode: false,
      images_light: [],
      images_dark: [],
      links: [
        "https://mp.weixin.qq.com/s/5FLRtO82riPNzPWidSTU5Q",
        "https://mp.weixin.qq.com/s/XUgsU1mPUvmDv7slZBs7jA",
        "https://mp.weixin.qq.com/s/n25l9taBRYNa6Vc_t22v8w",
        "https://mp.weixin.qq.com/s/rbn3iri9seWkO-YrlBjJDw",
        "https://mp.weixin.qq.com/s/xvHtMc2ncIEBWVuSF7rWVA",
        "https://mp.weixin.qq.com/s/5KkY3A8EgrPaojPyLoBP6A",
        "https://mp.weixin.qq.com/s/SruQxktqyVOVYzNm8wrUQQ",
        "https://mp.weixin.qq.com/s/OFDecjAKS6G_6c84xSX-TA",
        "https://mp.weixin.qq.com/s/pUExzkyRHuFjyFqFBm57Zw",
        "https://mp.weixin.qq.com/s/fwzdxZowNDwrskkVJq4VDA",
        "https://mp.weixin.qq.com/s/IpCqzjI8zRWNEcvwolJKmA",
        "https://mp.weixin.qq.com/s/81i2QryACeoWMbduATX8uQ",
        "https://mp.weixin.qq.com/s/PLjk2GmUuEn2Yj4uiEO13A",
        "https://mp.weixin.qq.com/s/TW3dqON3GieYmQVaLJ379A",
        "https://mp.weixin.qq.com/s/v8dOOv7LcWeB5KKqgsHT6A",
        "https://mp.weixin.qq.com/s/LuIfTeO6oqvjSFXMFW6V5w",
        "https://mp.weixin.qq.com/s/N55xQv2Qjue_eekpZuDNVQ",
        "https://mp.weixin.qq.com/s/jR8M0194YyrsfgTEh5y8EA",
        "https://mp.weixin.qq.com/s/6ZTFr1hRRKgqYDx34BJBew",
        "https://mp.weixin.qq.com/s/pH6GS7Vk35v7x6tDz-focg",
        "https://mp.weixin.qq.com/s/8NFynw7NBC7GDq4O79vQKQ",
        "https://mp.weixin.qq.com/s/2qK4qPCP3Jylo5vjhImu-A",
        "https://mp.weixin.qq.com/s/3IInltLPhFxr43oqA6Hh_g",
        "https://mp.weixin.qq.com/s/v5lr7t_tLqC5R6Tg-DIGSg",
        "https://mp.weixin.qq.com/s/3M7HXcQzIYw1ecOB_rEr3A",
        "https://mp.weixin.qq.com/s/otzcIx5oNRiEpQeQboKM6Q",
        "https://mp.weixin.qq.com/s/aIyhgVtJealK70XQ6vS3bw",
        "https://mp.weixin.qq.com/s/-StyEuXuxON_k4HRWZ6scQ",
        "https://mp.weixin.qq.com/s/AhDIVtCcrWVzZdMfowvCvg",
        "https://mp.weixin.qq.com/s/qA0wZnFUb399UbR-QM6PJw",
        "https://mp.weixin.qq.com/s/UVeVlT-wY90ithtB6gdGIA",
        "https://mp.weixin.qq.com/s/LGOBidd3X7ORissJ8QniMA",
        "https://mp.weixin.qq.com/s/XESMlkWXzE2Dx3clHxBFrQ",
        "https://mp.weixin.qq.com/s/rcM9niomL9IMRFIgNYcKeQ",]
    }
  },
  mounted: function () {
    if (/ipad|iphone|midp|rv:1.2.3.4|ucweb|android|windows ce|windows mobile/.test(navigator.userAgent.toLowerCase())) {
      this.class_name = "imgphoto_phone";
    }
    this.prepareImages();
    this.prepareKindPhotos();
    setInterval(this.checkdarkmod, 10);
  },
  methods: {
    prepareImages: function () {
      for (let i = this.links.length; i >= 1; i--) {
        this.images_light.push("./photos/" + i + ".png");
        this.images_dark.push("./photos_dark/" + i + ".png");
      }
    },
    prepareKindPhotos: function () {
      if (this.kind == "") {
        for (let i = 0; i < this.links.length; i++) {
          this.photo_see.push(true);
        }
      } else {
        for (let i = 0; i <= this.links.length; i++) {
          this.photo_see.push(false);
        }
        if (this.kind == "Department_culture") {
          for (let i = 0; i < this.photokind.Department_culture.length; i++) {
            this.photo_see[this.links.length - this.photokind.Department_culture[i]] = true;
          }
        } else if (this.kind == "Experience_sharing") {
          for (let i = 0; i < this.photokind.Experience_sharing.length; i++) {
            this.photo_see[this.links.length - this.photokind.Experience_sharing[i]] = true;
          }
        } else if (this.kind == "Document_sharing") {
          for (let i = 0; i < this.photokind.Document_sharing.length; i++) {
            this.photo_see[this.links.length - this.photokind.Document_sharing[i]] = true;
          }
        } else if (this.kind == "Medical_science_popularization") {
          for (let i = 0; i < this.photokind.Medical_science_popularization.length; i++) {
            this.photo_see[this.links.length - this.photokind.Medical_science_popularization[i]] = true;
          }
        } else if (this.kind == "Psychological_words") {
          for (let i = 0; i < this.photokind.Psychological_words.length; i++) {
            this.photo_see[this.links.length - this.photokind.Psychological_words[i]] = true;
          }
        } else if (this.kind == "Intensive_care") {
          for (let i = 0; i < this.photokind.Intensive_care.length; i++) {
            this.photo_see[this.links.length - this.photokind.Intensive_care[i]] = true;
          }
        } else if (this.kind == "Medical_English") {
          for (let i = 0; i < this.photokind.Medical_English.length; i++) {
            this.photo_see[this.links.length - this.photokind.Medical_English[i]] = true;
          }
        }
      }
    },
    checkdarkmod: function () {
      this.dark_mode = this.mediaQuery.matches;
    }
  },
}
</script>
<style>
iframe {
  background-color: #F1F1F1;
  width: 90%;
  height: 51vw;
  margin-bottom: 15px;
  min-height: 280px;
}

img.qr {
  width: 80%;
}

img.imgphoto_pc {
  min-height: 195px;
  margin: 10px;
  width: 45%;
  height: 35vw;
  transition: transform 0.5s;
}

img.imgphoto_pc:hover {
  transform: scale(1.1, 1.1);
}

img.imgphoto_phone {
  margin: 10px;
  width: 90%;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
}

div.photos {
  text-align: center;
  margin: 20px auto;
  width: 95%;
}

@media screen and (prefers-color-scheme: dark) {
  iframe {
    background-color: #1f1f1f;
  }
}
</style>