<template>
  <div class="entry-content" style="width:98%;">
    <div class="wp-block-cover alignfull has-background-dim" style="min-height:480px;">
      <div id="photo-list">
        <ul id="scroll">
          <img v-if="seen[0]" class="wp-block-cover__image-background" src="../assets/home_photos/1.jpg"
               style="object-position:50% 39%;"/>
          <img v-if="seen[1]" class="wp-block-cover__image-background" src="../assets/home_photos/2.jpg"
               style="object-position:50% 39%;"/>
          <img v-if="seen[2]" class="wp-block-cover__image-background" src="../assets/home_photos/3.jpg"
               style="object-position:50% 39%;"/>
          <img v-if="seen[3]" class="wp-block-cover__image-background" src="../assets/home_photos/4.jpg"
               style="object-position:50% 39%;"/>
          <img v-if="seen[4]" class="wp-block-cover__image-background" src="../assets/home_photos/5.jpg"
               style="object-position:50% 39%;"/>
        </ul>
      </div>
      <div class="main_face">
        <h1 style="text-align:center;font-size:60px;"><img class="ico" mode="widthFix" src="../assets/icon.svg"/>望江重症
        </h1>
        <p style="text-align:center;font-size:25px;">浙江省人民医院</p>
        <div class="eachline">
          <Button msg="重说纷纭" link="Experience_sharing" color="orange" width="_25"/>
          <Button msg="文献分享" link="Document_sharing" color="orange" width="_25"/>
          <Button msg="医学科普" link="Medical_science_popularization" color="orange" width="_25"/>
        </div>
        <div class="eachline">
          <Button msg="心理话" link="Psychological_words" color="green" width="_25"/>
          <Button msg="重症护理" link="Intensive_care" color="green" width="_25"/>
          <Button msg="医学英语" link="Medical_English" color="green" width="_25"/>
        </div>
        <div class="eachline">
          <Button msg="科室文化" link="Department_culture" color="luminous_back" width="_40"/>
          <Button msg="科室宣传片" link="Video" color="luminous" width="_40"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Button from './Button.vue'

export default {
  components: {
    Button
  },
  name: "Buttons",
  mounted() {
    setInterval(this.change_photo, 3800);
    setInterval(this.changeBackgroundColor, 10);
  },
  methods: {
    changeBackgroundColor: function () {
      var body = document.getElementById("body");
      var html = document.getElementById("body");
      if (!this.mediaQuery.matches) {
        body.style.setProperty('background-color', '#DEF9FF');
        html.style.setProperty('background-color', '#DEF9FF');
      } else {
        body.style.setProperty('background-color', '#012d33');
        html.style.setProperty('background-color', '#012d33');
      }
    },
    change_photo: function () {
      var a = this.seen[4];
      this.seen[4] = this.seen[3];
      this.seen[3] = this.seen[2];
      this.seen[2] = this.seen[1];
      this.seen[1] = this.seen[0];
      this.seen[0] = a;
      this.$forceUpdate();
    }
  },
  data() {
    return {
      seen:
          [true, false, false, false, false],
      mediaQuery: window.matchMedia('(prefers-color-scheme: dark)'),
    }
  }
}
</script>

<style scoped>
div.entry-content {
  margin: auto;
  margin-top: 1%;
}

.wp-block-cover {
  position: relative;
  display: flex;
  justify-content: center;
  padding: 1em;
  box-sizing: border-box
}

.has-background-dim {
  color: #fff;
  background-color: #1279be
}

.has-background-dim:before {
  content: "";
  background-color: inherit
}

.has-background-dim:not(.has-background-gradient):before {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
  opacity: .4
}

img.wp-block-cover__image-background {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

img.ico {
  margin-top: -10px;
  width: 70px;
}

img {
  vertical-align: middle
}

.entry-content > *:first-child {
  margin-top: 0;
}

.entry-content > *:last-child {
  margin-bottom: 0
}

.entry-content > .alignfull {
  margin: inherit;
  width: inherit;
  max-width: inherit
}

.eachline {
  display: flex;
  column-gap: .5em;
  justify-content: center;
  width: calc(calc(782px - 32px) + 257px);
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.main_face {
  z-index: 1;
  margin-top: 32px;
  margin-bottom: 32px;
  margin-left: auto;
  margin-right: auto;
  color: currentColor;
  width: calc(100% - 64px)
}

.main_face > * {
  margin-top: 32px;
  margin-bottom: 32px
}

.main_face > *:first-child {
  margin-top: 0
}

.main_face > *:last-child {
  margin-bottom: 0
}
</style>